class Helper{
    copyObject(obj){
        //deep copy
        return JSON.parse(JSON.stringify(obj));
    }
    removeElementFromArray(array, element){
        let index = array.indexOf(element);

        if (index > -1) {
            array.splice(index, 1);
        }
    }
}

let helper = new Helper;

export default helper;




